body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.dark-model {
    margin-right: 10px;
    width: 20px;
    height: 20px;
}
#app {
    background: #fff;
    height: 100%;
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column
}

#app #nav {
    position: fixed;
    z-index: 1000;
    left: 0;
    right: 0;
    height: 57px;
    line-height: 57px;
    text-align: center;
    background: white;
    font-weight: 700;
    font-size: 20px;
    color: #333;
    -webkit-box-shadow: 0px 4px 10px 0px rgba(0,0,0,.07);
    box-shadow: 0 4px 10px #00000012
}

#app #nav .back {
    position: absolute;
    left: 0;
    height: 100%;
    width: 50px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    cursor: pointer
}

#app #nav .translate {
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    width: 50px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    cursor: pointer
}

#app .demo {
    height: 100%;
    background: #f7f8fa;
    overflow-x: hidden;
    overflow-y: auto;
    padding: 57px 17px 0
}

#app .demo.no-overflow {
    overflow: unset
}

#app .demo.full {
    padding: 57px 0 0
}

#app .demo.full h2 {
    padding-left: 17px
}

#app .demo.bg-w {
    background: #fff
}

#app .demo::-webkit-scrollbar {
    width: 0;
    background: transparent
}

#app .demo>h2 {
    margin-top: 30px;
    margin-bottom: 10px;
    font-size: 14px;
    color: #909ca4;
    padding: 0 10px;
    font-weight: 400
}

#app .demo>p {
    font-size: 12px
}

#app .demo .card {
    padding: 25px 18px;
    background: rgb(255,255,255)
}

.nut-theme-dark .demo {
    background: #000!important
}

html,body,div,span,iframe,h1,h2,h3,h4,h5,h6,p,em,img,s,strong,b,u,i,dl,dt,dd,ol,ul,li,fieldset,form,label,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,section,time,audio,video {
    margin: 0;
    padding: 0;
    border: 0;
    font-family: PingFang SC,Microsoft YaHei,Helvetica,Hiragino Sans GB,SimSun,sans-serif;
    -webkit-overflow-scrolling: touch
}

